import React from "react";
import { Button } from "reactstrap";
import personIcon from "../assets/svgs/ic_person.svg";
import ethIcon from "../assets/svgs/ic_ethFill.svg";

const Asker = ({asker}) => {
  return asker && asker.length > 0 ? (
    <small><em>Asked by {asker}</em></small>
  ) : '';
}

const QuestionMain = ({ active, eth, answerQuestion }) => {
  return active && active.id ? (
    <div>
      <h1 className="px-3 pt-4">{active.text}</h1>
      <div className="pt-4">
        <Button
          className="btnAnswers"
          size="lg"
          color="success"
          onClick={() => answerQuestion(eth, active.id, 0)}
        >
          {active.answer1}
        </Button>{" "}
        <span className="p-2" />
        <Button
          className="btnAnswers"
          size="lg"
          color="success"
          onClick={() => answerQuestion(eth, active.id, 1)}
        >
          {active.answer2}
        </Button>{" "}
        <div className="my-4 pb-4">
          <img src={personIcon} alt="Number of Answers" /> {active.totalAnswers}/{
            active.maxAnswers
          }{" "}
          <span className="px-2" />
          <img src={ethIcon} alt="Ethereum Bounty" />{" "}
          {active.bounty ? active.bounty + " ETH" : ""}
        </div>
        <Asker asker={active.asker} />
        <div />
      </div>
    </div>
  ) : (
    ""
  );
};

export default QuestionMain;



