import { ETH_CONNECT } from '../constants/ActionTypes';
const initialState = {
  address: '',
};

const eth = (state = initialState, action) => {
  switch(action.type) {
    case 'web3/RECEIVE_ACCOUNT':
      return {
        ...state,
        address: action.address
      };

    case 'web3/CHANGE_ACCOUNT':
      return {
        ...state,
        address: action.address
      };
    case ETH_CONNECT:
      // action.payload.accounts().then(accounts => console.log('accounts', accounts));		
      return { ...state, ...action.payload };
    default:
      return state;
  }
}

export default eth;



