import { connect } from 'react-redux'
import { initQuestionCoopContract,
  appLoop,
  askQuestion,
  answerQuestion,
  toggleQuestionModal} from '../actions'
import * as config from '../constants/Constants';
import { TOGGLE_QUESTION_MODAL } from '../constants/ActionTypes';
import App from '../components/App'

function calcRemaining(state) {
  if(state.questions.count-1 > config.QUESTION_QUEUE_SIZE_MAX) {
    return (state.questions.count-1) - config.QUESTION_QUEUE_SIZE_MAX;
  }
  return 0;
}

const mapStateToProps = state => ({
  active: state.questions.active,
  queue: state.questions.queue.slice(1),
  remaining: calcRemaining(state),
  askedQuestions: state.questions.asked,
  answeredQuestions: state.questions.answered,
  eth: state.eth,
  modal: state.questions.modal,
  config: state.config,
});

export default connect(
  mapStateToProps,

  { initQuestionCoopContract,
    appLoop,
    askQuestion,
    answerQuestion,
    toggleQuestionModal},
)(App)



