import React from "react";
import CashoutButton from "../containers/CashoutButton";
import ethIcon from "../assets/svgs/ic_ethFill.svg";
import personIcon from "../assets/svgs/ic_person.svg";

const QuestionListItem = ({ question, id }) => {
  return (
    <li key={id} className="questionLI">
      <p className="listQuestion">{question.text}</p>
      <div className="listMeta align-middle">
        <img src={personIcon} alt="Number of Answers" />
        {"  "}
        {question.totalAnswers}/{question.maxAnswers} <span className="px-1" />{" "}
        <img src={ethIcon} alt="Ethereum Bounty" />
        {question.bounty}
        <div>{question.answer ? "Winning Answer: " + question.answer : ""}</div>
        <div>{question.myAnswer ? "My vote: " + question.myAnswer : ""}</div>
        <CashoutButton question={question} />
      </div>
      <hr />
    </li>
  );
};

export default QuestionListItem;



