import {
  QUESTIONS_GET_TOP,
  QUESTIONS_GET_TOP_SUCCESS,
  QUESTIONS_GET_TOP_FAILURE,
  QUESTIONS_GET_ASKED,
  QUESTIONS_GET_ASKED_SUCCESS,
  QUESTIONS_GET_ASKED_FAILURE,
  QUESTIONS_GET_ANSWERED,
  QUESTIONS_GET_ANSWERED_SUCCESS,
  QUESTIONS_GET_ANSWERED_FAILURE,
  QUESTIONS_GET_COUNT,
  TOGGLE_QUESTION_MODAL
} from '../constants/ActionTypes';

const initialState = {
  loading: false,
  queue: [],
  asked: [],
  answered: [],
  active: {},
  error: false,
  modal: false,
  count: 0
};

const questions = (state = initialState, action) => {
  switch (action.type) {
    case QUESTIONS_GET_TOP:
      return {
        ...state,
        loading: true,
        error: false,
      };
    case QUESTIONS_GET_TOP_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        queue: action.questions,
        active: action.questions.length > 0 ? action.questions[0] : initialState.active,
      };
    case QUESTIONS_GET_TOP_FAILURE:
      return {
        ...state,
        error: true,
      };
    case QUESTIONS_GET_ASKED:
      return {
        ...state,
        loading: true,
        error: false,
      };
    case QUESTIONS_GET_ASKED_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        asked: action.questions,
      };
    case QUESTIONS_GET_ASKED_FAILURE:
      return {
        ...state,
        error: true,
      };
    case QUESTIONS_GET_ANSWERED:
      return {
        ...state,
        loading: true,
        error: false,
      };
    case QUESTIONS_GET_ANSWERED_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        answered: action.questions,
      };
    case QUESTIONS_GET_ANSWERED_FAILURE:
      return {
        ...state,
        error: true,
      };
    case TOGGLE_QUESTION_MODAL:
      return {
        ...state,
        modal: !state.modal,
      };
    case QUESTIONS_GET_COUNT:
      return {
        ...state,
        count: action.count,
      };
    default:
      return state;
  }
};

export default questions;



