import { connect } from 'react-redux';
import NavStats from '../components/NavStats';
import {setNickname} from '../actions';
 
const mapStateToProps = (state, ownProps) => ({
  user: state.user,
  eth: state.eth,
})
 
export default connect(
  mapStateToProps,
  { setNickname },
)(NavStats);



