import React from "react";
import TransactionItem from "./TransactionItem";
import { Row, Col } from "reactstrap";

const TransactionsPending = ({ user }) => {
  return user.transactions && user.transactions.length > 0 ? (
    <div>
      <Row>
        <Col>          
          <ul className="upNextList">
            {user.transactions.map((prop, k) => {
              return <TransactionItem transaction={prop} id={k} key={k} />;
            })}
          </ul>
        </Col>
      </Row>
    </div>
  ) : (
    ""
  );
};

export default TransactionsPending;



