import React from "react";
import QuestionListItem from "./QuestionListItem";
import { Row, Col } from "reactstrap";

const MoreText = ({remaining}) => {
  return remaining && remaining > 0 ?
  (
    <li>+ {remaining} more</li>
  ) : '';
}

const QuestionList = ({ title, questions, remaining  }) => {
  return questions && questions.length > 0 ? (
    <div>
      <Row>
        <Col>
          <h3 className="text-center pb-2">{title}</h3>
          <ul className="upNextList">
            {questions.map((prop, k) => {
              return <QuestionListItem question={prop} id={k} key={k} />;
            })}
            <MoreText remaining={remaining} />
          </ul>
        </Col>
      </Row>
    </div>
  ) : (
    ""
  );
};

export default QuestionList;



