import React from "react";
import {
  Col,
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  ModalBody,
  ModalHeader,
  ModalFooter
} from "reactstrap";

const QuestionForm = ({ handleSubmit, handleChange,getMaxAnswersByBounty, config }) => {
  return (
    <div style={{ backgroundColor: "white", color: "grey" }}>
      <ModalHeader>Ask a Question</ModalHeader>
      <ModalBody>
        <Form onSubmit={handleSubmit}>
          <FormGroup row>
            <Label sm={2}>Question: </Label>
            <Col sm={10}>
              <Input type="textarea" onChange={handleChange} name="question" />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={2}>Answers: </Label>
            <Col sm={5}>
              <Input
                type="text"
                onChange={handleChange}
                name="answer1"
                placeholder="Answer 1"
              />
            </Col>
            <Col sm={5}>
              <Input
                type="text"
                onChange={handleChange}
                name="answer2"
                placeholder="Answer 2"
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={2}>Bounty: </Label>
            <Col sm={10}>
              <Input type="text" onChange={(event) => {
                  getMaxAnswersByBounty(event.target.value);
                  handleChange(event);
                }} name="bounty" />
              <small><em>{config.currentQuestion ? "* less fee of " + config.currentQuestion.fee + ' ETH' : ''}</em></small>
            </Col>
          </FormGroup>
          <FormGroup row>
            <Col sm={7}>
              <Label>How man can answer?: </Label><br />
                <em>Min: <strong>{config.minAnswers}</strong> Max: <strong>
                  {(config.currentQuestion && config.currentQuestion.maxAnswersByBounty) ?
                    (config.currentQuestion.maxAnswersByBounty <= config.maxAnswers ? config.currentQuestion.maxAnswersByBounty : config.maxAnswers)
                    : ''}
                  </strong></em>
              </Col>
            <Col sm={5}>
              <Input type="text" onChange={handleChange} name="answers"  />
            </Col>
          </FormGroup>
        </Form>
      </ModalBody>
      <ModalFooter>
        <Button onClick={handleSubmit}>Submit</Button>
      </ModalFooter>
    </div>
  );
};

export default QuestionForm;



