import { CONFIG_LOADED,
CONFIG_MAX_ANSWERS_BY_BOUNTY } from '../constants/ActionTypes';
const initialState = {};

const config = (state = initialState, action) => {
  switch(action.type) {
    case CONFIG_LOADED:
      return {
        ...state,
        ...action.config,
      };
    case CONFIG_MAX_ANSWERS_BY_BOUNTY:
      return {
        ...state,
        currentQuestion: {
          fee: action.fee,
          bounty: action.bounty,
          maxAnswersByBounty: action.maxAnswersByBounty,
        }
      };
    default:
      return state;
  }
}

export default config;



