const providerText = {
  'metamask': 'MetaMask',
  'trust': 'Trust Wallet',
  'default': 'Provider',
};

export const providerName = (provider) => {
  if(!provider) {
    return providerText['default'];
  }
  if(provider.isMetaMask) {
    return providerText['metamask'];
  }
  if(provider.isTrust) {
    return providerText['trust'];
  }
  return providerText['default'];
}



