import { connect } from 'react-redux'
import {cashout} from '../actions'
import CashoutButton from '../components/CashoutButton'
 
const mapStateToProps = (state, ownProps) => ({
  question: ownProps.question,
  eth: state.eth,
})
 
export default connect(
  mapStateToProps,
  { cashout },
)(CashoutButton)



