import React, { Component } from "react";
import {
  Button,
  Container,
  Col,
  Row,
  Modal,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane
} from "reactstrap";
import NavHeader from "./NavHeader";
import { REFRESH_INTERVAL } from "../constants/Constants";
import QuestionList from "./QuestionList";
import QuestionForm from "../containers/QuestionForm";
import TransactionsPending from "../containers/TransactionsPending";
import QuestionMain from "./QuestionMain";
import classnames from "classnames";
import { providerName } from "../util/provider"

class App extends Component {
  constructor(props) {
    super(props);

    this.props.initQuestionCoopContract(this.props.eth);
    this.props.appLoop(this.props.eth);
    setInterval(() => {
      this.props.appLoop(this.props.eth);
    }, REFRESH_INTERVAL);

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.toggle = this.toggle.bind(this);
    this.toggleTab = this.toggleTab.bind(this);
    this.state = {
      activeTab: "1"
    };
  }

  toggle() {
    this.props.toggleQuestionModal();
  }

  toggleTab(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  handleSubmit(event) {
    if (parseInt(this.state["answers"], 10) < this.props.config.minAnswers) {
      alert(
        "You must specify at least " +
          this.props.config.minAnswers +
          " answers!"
      );
      return;
    }

    if (parseInt(this.state["answers"], 10) > this.props.config.maxAnswers) {
      alert(
        "You must specify no more than " +
          this.props.config.maxAnswers +
          " answers!"
      );
      return;
    }

    if (parseInt(this.state["answers"], 10) > this.props.config.currentQuestion.maxAnswersByBounty) {
      alert(
        "You must specify no more than " +
          this.props.config.currentQuestion.maxAnswersByBounty +
          " answers! If you want to allow more answers, you must set a higher bounty."
      );
      return;
    }

    if (parseInt(this.state["answers"], 10) % 2  === 0) {
      alert(
        "Number of participants must be odd!"
      );
      return;
    }

    this.props.askQuestion(
      this.props.eth,
      this.state["question"],
      this.state["answer1"],
      this.state["answer2"],
      this.state["answers"],
      parseFloat(this.state["bounty"], 10)
    );

    this.toggle();
    event.preventDefault();
    alert('Question sent to '+providerName(this.props.eth.currentProvider)+'.  Please submit to complete the process.');
  }

  handleChange(event) {
    const target = event.target;
    this.setState({
      [target.name]: target.type === "checkbox" ? target.checked : target.value
    });
  }

  render() {
    return (
      <Container fluid>
        <NavHeader />
        <Row className="justify-content-center mainContent">
          <Col
            sm={{ size: "3", order: 1 }}
            xs={{ order: 2 }}
            className="NextQuestion pt-4"
          >
            <QuestionList title={"Up Next"} questions={this.props.queue} remaining={this.props.remaining} />
          </Col>
          <Col
            sm={{ size: "6", order: 2 }}
            xs={{ order: 1 }}
            className="text-center py-5 mainQuestion"
          >
            <QuestionMain
              active={this.props.active}
              eth={this.props.eth}
              answerQuestion={this.props.answerQuestion}
            />
          <TransactionsPending />
          </Col>
          <Col
            sm={{ size: "3" }}
            xs={{ order: 3 }}
            className="sideBar hidden-md-down"
          >
            <Row className="askQuestion justify-content-center mb-4 py-5">
              <div className="text-center">
                <p className="px-3">Do you have a This or That question?</p>
                <Button onClick={this.toggle} className="btnAsk" size="lg">
                  Ask a Question
                </Button>
              </div>
            </Row>
            <Modal isOpen={this.props.modal} toggle={this.toggle}>
              <QuestionForm
                handleChange={this.handleChange}
                handleSubmit={this.handleSubmit}
              />
            </Modal>
            <Nav tabs>
              <NavItem>
                <NavLink
                  className={classnames({
                    active: this.state.activeTab === "1"
                  })}
                  onClick={() => {
                    this.toggleTab("1");
                  }}
                >
                  Past Questions
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={classnames({
                    active: this.state.activeTab === "2"
                  })}
                  onClick={() => {
                    this.toggleTab("2");
                  }}
                >
                  Your Questions
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={this.state.activeTab}>
              <TabPane tabId="1">
                <QuestionList questions={this.props.answeredQuestions} />
              </TabPane>

              <TabPane tabId="2">
                <QuestionList questions={this.props.askedQuestions} />
              </TabPane>
            </TabContent>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default App;



