function splitEvery(str, n)
{
  var arr = [];
  for (var i = 0; i < str.length; i += n)
  {
    arr.push(str.substr(i, n));
  }
  return arr;
}

export function stringToByteArray(web3,string, length) {
  if(web3) {
    var len = 64;
    var hex = splitEvery(web3.fromAscii(string).substring(2), len);
    var byteArray = new Array(length);

    for(var i = 0; i < length; i++) {
      var str = "0";
      if(i < hex.length) {
        str = hex[i];
      }
      byteArray[i] = "0x"+str.padEnd(len,"0");
    }
    return byteArray;
  }
  return null;
}

export function byteArrayToString(web3, byteArray) {
  if(web3) {
    return byteArray.map((item) => {return web3.toAscii(item);}).reduce((acc, val, idx) => { return acc + val });
  }
  return null;
}

export function precisionRound(number, precision) {
  var factor = Math.pow(10, precision);
  return Math.round(number * factor) / factor;
}

export default {stringToByteArray, byteArrayToString, precisionRound}



