import React from "react";
import { Navbar, NavbarBrand } from "reactstrap";
import mainLogo from "../assets/imgs/Logo-wWhiteTag.png";
import NavStats from "../containers/NavStats";

export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar dark className="p-3 navbar-expand-lg">
          <img src={mainLogo} alt="FreeRange" />
          <NavbarBrand className="pl-3" href="/">
            This or That
          </NavbarBrand>
          <NavStats />
        </Navbar>
      </div>
    );
  }
}



