import { STATS_LOADED,TRANSACTION_UPDATE } from '../constants/ActionTypes';
const initialState = {
  address: 'Not logged in',
  cashedOutBalance: 0.00000,
  transactions: [],
};

const user = (state = initialState, action) => {
  switch(action.type) {
    case STATS_LOADED:
      return {
        ...state,
        cashedOutBalance: action.cashedOutBalance,
        address: action.address,
        nickname: action.nickname,
      };
    case TRANSACTION_UPDATE:
      return {
        ...state,
        transactions: action.transactions,
      };
    default:
      return state;
  }
}

export default user;



