import React from "react";
import { Button } from "reactstrap";

const CashoutButton = ({ question, cashout, eth }) => {
  function cashoutButtonClicked() {
    cashout(eth, question);
  }

  return question.canCashOut ? (
    <div>
      <Button onClick={cashoutButtonClicked} color="primary" size="sm">
        Cashout
      </Button>
    </div>
  ) : (
    ""
  );
};

export default CashoutButton;



